.page{height:100%;width:100%;display:flex;justify-content:center;padding-bottom:20px}.container{width:calc(100% - 40px);height:100%;max-width:1000px;margin-left:auto;margin-right:auto}h1{text-align:center}a{text-decoration:none;all:inherit;cursor:pointer}#navbar{position:fixed;z-index:1000;width:100%;height:75px;top:0;left:0;display:block}.navbar{background-image:-webkit-gradient(linear, left top, right top, from(#004551), to(#0ca890));background-image:linear-gradient(to right, #004551, #0ca890);color:white;-webkit-transition:color .1s, background-image .2s, background-color .2s;transition:color .1s, background-image .2s, background-color .2s}.navbar-scroll{background-color:white;color:#004551;box-shadow:0 0 6px black;-webkit-transition:color .1s, background-image .2s;transition:color .1s, background-image .2s}.image-header{display:flex;align-items:center;justify-content:start;margin-top:auto;margin-bottom:auto;width:200px;height:75px;margin-left:5px;background:transparent;box-shadow:none}.image-header:hover{cursor:pointer}.image-header:hover .mike-image{-webkit-filter:brightness(0.7);filter:brightness(0.7);-webkit-transition:-webkit-filter .2s;transition:-webkit-filter .2s;transition:filter .2s;transition:filter .2s, -webkit-filter .2s}.name{text-align:left;font-size:12px;font-weight:700}.mike-image{width:80px;height:80px;-webkit-clip-path:circle(35%);clip-path:circle(35%);-webkit-filter:brightness(1);filter:brightness(1);-webkit-transition:-webkit-filter .2s;transition:-webkit-filter .2s;transition:filter .2s;transition:filter .2s, -webkit-filter .2s}.contact-label{height:25px;width:60px;display:flex;justify-content:center;align-items:center;margin-left:auto;margin-top:20px;margin-right:20px;border:2px solid white;padding:5px 8px 5px 8px;border-radius:10px;font-weight:600;color:white;background-color:transparent;-webkit-transition:color .2s, background-color .1s, border .5s ease-out;transition:color .2s, background-color .1s, border .5s ease-out}.contact-label:hover{background-color:white;color:#004551;border:2px solid white;cursor:pointer}.contact-label-scroll{height:25px;width:60px;display:flex;justify-content:center;align-items:center;margin-left:auto;margin-top:20px;margin-right:20px;border:2px solid #004551;padding:5px 8px 5px 8px;border-radius:10px;font-weight:600;margin-left:auto;margin-right:20px;color:#004551;background-color:transparent;-webkit-transition:color .2s, background-color .1s, border .5s ease-out;transition:color .2s, background-color .1s, border .5s ease-out}.contact-label-scroll:hover{background:#004551;color:white;border:2px solid #004551;box-shadow:0 0 2px black;cursor:pointer}#title-page{height:60vh;width:100%;color:white;display:flex;align-items:center}.title-bg-wrapper{height:60vh;width:100%;position:absolute;z-index:-1}.title-bg{width:100%;height:100%;position:relative;left:0;bottom:0;z-index:-1}.title-elements{width:100%;display:flex;margin-top:-30px;flex-direction:column;justify-items:center;align-items:center}.dot-container{display:flex;align-items:center}.dot{width:20px;height:20px;background-color:#0ca890;-webkit-clip-path:circle(30%);clip-path:circle(30%);margin-right:5px}#mybg-title{font-size:18px;font-weight:800}#mybg-description{margin-top:2px;margin-bottom:10px;padding-left:27px;font-size:14px;color:#1c1c1c}#projects{width:100%;height:100%;display:flex;justify-content:center;padding-bottom:20px;background-color:#f2f2f2}.project-container{display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-around}.project-box{display:grid;align-items:center;justify-items:center;grid-template-rows:40px 180px 60px 150px;grid-template-columns:300px;overflow:hidden;box-shadow:0 0 2px black;border-radius:20px;margin-bottom:20px;margin-right:10px}.project-box:hover{box-shadow:0 2px 8px black}.project-box:hover .project-image{-webkit-transition:all .5s;transition:all .5s;-webkit-transform:scale(1.08);transform:scale(1.08)}.project-title{font-weight:800;font-size:1.4em;padding-top:15px;width:100%;height:100%;z-index:2;text-align:center;background-color:white}.project-image{width:100%;height:100%;overflow:hidden;z-index:1}.icon-container{width:90%;height:100%;background-color:white;padding-left:25px;padding-right:25px;z-index:2;display:flex;justify-content:space-around;align-items:center}#project-icon{width:40px;height:40px}.description-wrapper{width:100%;height:100%;background:white;display:flex;align-items:center;justify-content:center}.project-description{height:90%;width:90%;font-size:14px}.description-link-wrapper{height:50px;width:100%;display:flex;justify-content:space-around;background-color:white;margin-top:10px;margin-bottom:10px}.descrption-link{width:100px;height:40px;display:flex;justify-content:center;align-items:center;background-color:white;border:2px solid #004551;border-radius:10px;font-weight:600;font-size:16px;color:#004551}.descrption-link:hover{background-color:#004551;color:white}.logo-container{width:80%;max-width:320px;margin-left:auto;margin-right:auto;display:flex;justify-content:center;margin-bottom:20px}.logo{width:100%;height:100%;margin-left:auto;margin-right:auto;text-align:center}.fcc-certification-container{width:100%;display:flex;flex-wrap:wrap;justify-content:space-around;font-family:'Roboto Mono', monospace}.certification-button{border:2px solid #0a0a23;padding:10px;width:350px;text-align:center;background-color:#d0d0d5}.certification-button:hover{background-color:#0a0a23;color:white}.certifications{margin-left:10px;margin-bottom:20px}.certification-section{margin-top:10px;margin-bottom:10px;display:flex;align-items:center}.down-arrow{height:12px;width:12px;background-color:#0a0a23;-webkit-clip-path:polygon(50% 100%, 0 0, 100% 0);clip-path:polygon(50% 100%, 0 0, 100% 0)}.certification-header{padding-left:8px}.course-contents{margin-left:30px}.content{display:flex;align-items:center}.check{width:12px;height:12px;margin-right:7px}.google-certification-container{width:100%;display:flex;flex-wrap:wrap;margin-top:20px;justify-content:space-around;font-family:'Roboto', sans-serif}.google-certification{display:flex;align-items:center;margin-bottom:20px}.google-certification-logo{width:60px;margin-right:5px}.google-certification-description{color:#3f464b;width:290px}.g-cert-descript{font-weight:600;margin-bottom:10px}#contact{background-color:#f2f2f2;padding-bottom:40px}.contact-content-wrapper{width:100%;display:flex;flex-wrap:wrap;align-items:center;justify-content:space-around}.contact-content{display:flex;font-size:18px;margin:5px;min-width:220px}.contact-email{font-weight:700}.contact-info{padding-left:2px}.contact-number{font-size:14px;padding:2px 2px 2px 2px;width:140px;position:absolute;z-index:9;padding-left:6px;-webkit-filter:blur(3px);filter:blur(3px);-webkit-transition:-webkit-filter .2s ease-in;transition:-webkit-filter .2s ease-in;transition:filter .2s ease-in;transition:filter .2s ease-in, -webkit-filter .2s ease-in}.contact-number:hover{-webkit-filter:blur(0);filter:blur(0);-webkit-transition:-webkit-filter .2s ease-in;transition:-webkit-filter .2s ease-in;transition:filter .2s ease-in;transition:filter .2s ease-in, -webkit-filter .2s ease-in}#footer{width:100%;background-color:#004551;background-image:-webkit-gradient(linear, left top, right top, from(#004551), to(#0ca890));background-image:linear-gradient(to right, #004551, #0ca890);color:white;padding-top:15px;padding-bottom:15px}.footer-links{display:flex;justify-content:space-around;width:300px;margin-left:auto;margin-right:auto}.footer-link{margin-right:20px;padding:3px;height:40px;width:40px;display:flex;justify-content:center;align-content:center;border-bottom:2px solid transparent;object-fit:cover}.footer-link .footer-img{max-width:90%;max-height:90%;-webkit-filter:invert(100%);filter:invert(100%)}.footer-link:hover{border-bottom:2px solid white}.footer-link:hover .footer-img{-webkit-filter:invert(100%) brightness(0.03) sepia(20) saturate(80) hue-rotate(540deg);filter:invert(100%) brightness(0.03) sepia(20) saturate(80) hue-rotate(540deg)}.copyright{text-align:center;margin-top:10px}#other-skills{background-color:#f2f2f2;text-align:center}.skills-container{margin-left:auto;margin-right:auto;display:flex;flex-wrap:wrap;justify-content:space-around}.skill-box{display:inline-block;border:1px solid gray;border-radius:10px;text-align:center;background:transparent;width:300px;height:250px;margin-left:20%;margin-right:20%;margin-bottom:20px;z-index:2}.skill-title{background:-webkit-gradient(linear, left top, right top, from(#004551), to(#0ca890));background:linear-gradient(to right, #004551, #0ca890);border-radius:8px 8px 0px 0px;padding:5px 0px 5px 0px;color:white;font-weight:600;height:25px;position:relative;left:0px;top:0px;z-index:0}.skills{text-align:left;font-size:14px}ul{list-style-type:none;margin:0;padding:0;padding-left:5px}li{padding-top:7px}.skills-experience{-webkit-transform:rotate(270deg);transform:rotate(270deg);color:#004551;font-size:18px;font-weight:700}.skills-bar{height:140px;width:2px;background:-webkit-gradient(linear, left top, right top, from(#004551), to(#0ca890));background:linear-gradient(to right, #004551, #0ca890);position:relative;left:40px}.skills-logo-container{display:flex;flex-wrap:wrap;justify-content:space-evenly}.skills-logo{max-height:20px;max-width:60px;-webkit-filter:brightness(0);filter:brightness(0)}button{border-radius:10px;border:2px solid #004551;background-color:white;height:40px;width:100px}button:hover{background-color:#004551;cursor:pointer}button:hover .skills-logo{-webkit-filter:brightness(0) invert(1);filter:brightness(0) invert(1)}@media screen and (max-width: 400px){.logo{max-width:300px}.certification-button{font-size:14px;width:280px}.content{font-size:12px}.google-certification{font-size:12px}.google-certification-description{width:220px}#footer{padding-top:20px;padding-bottom:30px}}.fade-in{opacity:0;-webkit-transition:opacity 1s;transition:opacity 1s}.fade-in.appear{opacity:1}.fade-in-slow{opacity:0;-webkit-transition:opacity 2s;transition:opacity 2s}.fade-in-slow.appear{opacity:1}

